import React from 'react'
import axios from 'axios'
import { Col, Row, Container, InputGroup, FormControl} from 'react-bootstrap'
import { Link } from "react-router-dom";
import Header from './header'
import swal from "sweetalert";
import Cookies from 'js-cookie'

class Request extends React.Component {

componentDidMount() {
  let user = Cookies.get('name')
  console.log(user)
  this.setState({ 
    username: user,
})
}
  state = {request: ''}

  handleSubmit = async (event) => {
    event.preventDefault();
      axios
        .post('/request', {'request':`${this.state.request}`, 'user': `${this.state.username}`})
        .then(res => {
          if(res.data.result === "success") {
            swal("Thank you!", res.data.message, "success") } else if (res.data.result === "error") {
              swal("Error!", res.data.message, "error");
            }      })
        .catch(error => {
          console.log(error);
        });
  };


	render() {

    return (
      <>
      <Header /><Container fluid>
      <Row xs={12} style={{marginTop:"3%"}}className="accordian text-center">
        <Col xs={12} className="requestMovie">
          <h2>MOVIE REQUEST</h2>
          <i>Have something in mind that you want to watch?</i>
            <Row  className="justify-content-center"xs={2} md={2} lg={2} style={{marginTop:'1%'}}>
            <form onSubmit={this.handleSubmit}>
              <InputGroup  size="lg">
                <FormControl value={this.state.request} onChange={event => this.setState({request: event.target.value})} placeholder="Request movie by title..." aria-label="Large"  aria-describedby="inputGroup-sizing-sm" />

              </InputGroup>
              </form>
            </Row>

            <p style={{fontSize:'12px'}}>For any other inquiry, please <Link to={'/contact'}>click here</Link> to use our contact page.</p>
</Col></Row>
       </Container>
      </>
    )
  }
}

export default Request