import React, { useRef, useCallback, useState, useEffect } from 'react'
import axios from 'axios'
import { Col, Row, Container, Spinner, InputGroup, FormControl, Button} from 'react-bootstrap'
import { Link } from "react-router-dom";
import Header from './header'
import Request from './request'

function useMovieSearch(query, pageNumber) {
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(false)
    const [movies, setMovies] = useState([])
    const [hasMore, setHasMore] = useState(false)
  
    useEffect(() => {
      setMovies([])
    }, [query])
  
    useEffect(() => {
      setLoading(true)
      setError(false)
      let cancel
      axios({
        method: 'GET',
        url: `https://lp-server1.lastplay.tv/mov/api/movie?apikey=${process.env.REACT_APP_RADARR}&pageSize=3000`,
        params: {page: pageNumber },
        cancelToken: new axios.CancelToken(c => cancel = c)
      })

      .then(res => {
        let data = res.data.records.filter(m => m.hasFile)
        data = data.sort((a, b) => b.movieFile.dateAdded.localeCompare(a.movieFile.dateAdded))
        setMovies(prevMovies => {
          return [...new Set([...prevMovies, ...data.map(m => m)])]
        })
        setHasMore(res.data.records.length > 0)
        setLoading(false)
      }).catch(e => {
        if (axios.isCancel(e)) return
        setError(true)
      })
      return () => cancel()

    }, [query, pageNumber])
  
    return { loading, error, movies, hasMore }
  }



    
function Movie() {
    const [query] = useState('')
    const [pageNumber, setPageNumber] = useState(1)
  
    const {
      movies,
      hasMore,
      loading,
      error
    } = useMovieSearch(query, pageNumber)
  
    const observer = useRef()
    const lastMovieElementRef = useCallback(node => {
      if (loading) return
      if (observer.current) observer.current.disconnect()
      observer.current = new IntersectionObserver(entries => {
        if (entries[0].isIntersecting && hasMore) {
          setPageNumber(prevPageNumber => prevPageNumber + 1)
        }
      })
      if (node) observer.current.observe(node)
    }, [loading, hasMore])
  

    return (
      <>
      <Header />
      <Request />
      <Container fluid>
        
 
      <Row xs={12} style={{marginTop:"3%"}}className="accordian text-center">

        {movies.map((movie, index) => {
          if(movie.hasFile) {

          
          if (movies.length === index + 1) {
            return (
              <Col xs={6} sm className='shows' key={`${movie.id}`}>
              <Link ref={lastMovieElementRef} to={{
                  pathname:`/movie/${movie.id}`, state: {
                          tmd: movie.tmdbId}}}>
                  <img src={`https://lp-server1.lastplay.tv/mov/MediaCover/${movie.id}/poster-250.jpg`} alt={`${movie.title} poster`}/> <br />
                  <span>{movie.title}</span>
              </Link>
          </Col>
            )
          } else {
            return (
              <Col xs={6} sm className='shows' key={`${movie.id}`}>
              <Link to={{
                  pathname:`/movie/${movie.id}`, state: {
                          tmd: movie.tmdbId}}}>
                  <img src={`https://lp-server1.lastplay.tv/mov/MediaCover/${movie.id}/poster-250.jpg`} alt={`${movie.title} poster`}/> <br />
                  <span>{movie.title}</span>
              </Link>
          </Col>)          } } else{
             return(<div ref={lastMovieElementRef}></div>)
          }
        })}
        </Row>
        </Container>
        <Container>
<Row>
        
        <div>{loading && <Col className="text-center">
          <Spinner animation="border" role="status">
  <span className="sr-only">Loading...</span>
</Spinner>
        </Col>}</div>
        <div>{error && 'Error'}</div>
        </Row>
        </Container>
      </>
    )
  }

export default Movie